@import url(https://fonts.googleapis.com/css?family=Indie+Flower|Merriweather);
body {
  font-family: "Merriweather", serif;
}
 .wrapper{
   background: linear-gradient(to right, #e67e22, #e74c3c);
   height: 100vh;
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items:center;
           align-items:center;
 }

 .main{
   height: 90vh;
   /* box-shadow: 0px 13px 40px -13px rgba (0,0,0,0.75); */
   width: 80%;
   margin: 0 auto;
 }

 .title-container{
   height: 90vh;
   width:80vh;
   background: url(/simpleweatherapp/static/media/background.b6796c73.jpg)center center no-repeat;
   background-size: cover;
   display: -webkit-flex;
   display: flex;
   -webkit-align-items: center;
           align-items: center;
   -webkit-justify-content: center;
           justify-content: center;
   text-align: center;
   color: #000;
   box-shadow: 0px 13px 40px -13px rgba (0,0,0,0.75);
 }

 .title-container h1{
   font-size: 50px;
   letter-spacing: 2px;
   line-height: 1.3;
 }

 .title-container p{
   font-style: italic;
   font-weight: 100;
   letter-spacing: 1px;
   line-height: 1.5;
 }

.form-container{
  background-color: #2c3e50;
  height:90vh;
  width: 70vh;
  padding-top: 100px;
  padding-left: 50px;
  box-shadow: 0px 13px 40px -13px rgba (0,0,0,0.75);
}

input[type="zip"]{
  background-color: transparent;
  border: solid 1px #f16051;
  width: 35%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

 input[type="zip"]{
  outline:none;
} 

input:-webkit-autofill{
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

button{
  border:0;
  padding: 8px 20px;
  margin:0 2px;
  border-radius: 2px;
  font-weight:lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}

button:active{
  outline: #fff;
}

.weather{
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  color: #fff;
  margin-top: 25px;
}

.weather__error{
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight:200;
}
